<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <el-page-header @back="goBack" content="订单详情">
                        </el-page-header>
                        <!-- 订单信息 -->
                        <div class="flex f-nowrap j-bet uc_od_head">
                            <div>
                                <span class="mr15">订单编号：{{info.order_code}}</span>
                                <span>下单时间：{{info.time}}</span>
                            </div>
                            <div>
                                <el-button size="small" @click="cancelOrder(order_id)" v-if="info.state=='待付款'">取消订单</el-button>
                                <el-button class="btn" size="small" type="primary" v-if="info.state=='配送中'||info.state=='已付款'" @click="orderConfirm(order_id)">确认收货</el-button>
                                <el-button class="btn" size="small" v-if="info.state=='已签收'&&info.is_comment==0" plain type="primary" @click="toEvaluate(order_id)">评价</el-button>
                                <el-button class="btn f16" size="small" type="text" v-if="info.state=='已签收'&&info.is_comment==1" disabled>已评价</el-button>
                                <el-button class="btn" size="small" v-if="info.state=='待付款'" type="warning" plain @click="payNow(order_id)">立即付款</el-button>
                                <span :class="info.state=='待付款'?'colorPrice stateText':info.state=='取消订单'?'color9 stateText':'colorGreen stateText'">{{info.state}}</span>
                            </div>
                        </div>
                        <div class="ucod_pro flex f-nowrap j-bet" v-for="(item,index) in info.goods" :key="index">
                            <div class="flex f-nowrap od_des" >
                                <div class="od_img"><img :src="item.pic"></div>
                                <div class="od_info">
                                    <h2>{{item.name}}</h2>
                                    <p>规格：{{item.spec_name}}{{item.spec_unit}}</p>
                                </div>
                            </div>
                            <div class="od_uPrice tc">￥{{item.price}}</div>
                            <div class="od_num tc">数量：×{{item.number}}</div>
                            <div class="od_price tr">
<!--                                {{ (item.price*item.number).toFixed(2)}}-->
                                <p class="fb color3 f16 mt5">￥{{item.itotal}}</p>
<!--                                <p class="color9 f12">(含运费：￥0.00元)</p>-->
<!--                                <p class="color9 f12">(已支付订金100.00)</p>-->
                            </div>
                        </div>
                        <!-- 收货信息 -->
                        <div v-if="info.address!=''">
                            <div class="flex f-nowrap j-bet uoD_tit">
                                <h1 class="order_tit">收货信息</h1>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">收货人：</div>
                                <div class="uoD_rg">{{info.username}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">联系电话：</div>
                                <div class="uoD_rg">{{info.mobile}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">收货地址：</div>
                                <div class="uoD_rg">{{info.address}}</div>
                            </div>
                        </div>
                        <div class="hr mt15"></div>
                        <!-- 配送信息 -->
                        <div class="flex f-nowrap j-bet uoD_send" v-if="info.logistics">
                            <div class="uoD_s_lf">
                                <h1 class="order_tit mt15">配送信息</h1>
                                <div class="uoD_item">
                                    <div class="uoD_lf">配送方式：</div>
                                    <div class="uoD_rg">物流配送</div>
                                </div>
                                <div class="uoD_item">
                                    <div class="uoD_lf">物流状态：</div>
                                    <div class="uoD_rg colorGreen">{{info.logistics.code==12010?'暂无快递物流信息':info.logistics.data[0].status}}</div>
                                </div>
                                <div class="uoD_item" v-if="info.logistics.data">
                                    <div class="uoD_lf">承运快递：</div>
                                    <div class="uoD_rg">{{info.logistics.data[0].brand}}</div>
                                </div>
                                <div class="uoD_item" v-if="info.logistics.data">
                                    <div class="uoD_lf">运单编号：</div>
                                    <div class="uoD_rg">{{info.logistics.data[0].no}}</div>
                                </div>
                            </div>
                            <div class="uoD_s_rg mt15" v-if="info.logistics.data">
                                <div class="us_box">
                                    <div :class="index==0?'us_item active':'us_item'" v-for="(item,index) in info.logistics.data[0].data" :key="index">
                                        <span class="us_spot"></span>
                                        <p>{{item.context}}</p>
                                        <p>{{item.time}}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="hr mt15"></div>
                        <!-- 发票信息 -->
                        <div v-if="info.invoice">
                            <div class="flex f-nowrap j-bet uoD_tit">
                                <h1 class="order_tit">发票信息</h1>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">发票类型：</div>
                                <div class="uoD_rg">{{info.invoice.type==0?'企业':'个人'}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">发票抬头：</div>
                                <div class="uoD_rg">{{info.invoice.type==0?info.invoice.ename:info.invoice.title}}</div>
                            </div>
                            <div class="uoD_item" v-if="info.invoice.type==0">
                                <div class="uoD_lf">税号：</div>
                                <div class="uoD _rg">{{info.invoice.taxnumber}}</div>
                            </div>
                            <div class="uoD_item" v-if="info.invoice.type==0">
                                <div class="uoD_lf">开户银行：</div>
                                <div class="uoD _rg">{{info.invoice.bank}}</div>
                            </div>
                            <div class="uoD_item" v-if="info.invoice.type==0">
                                <div class="uoD_lf">银行账号：</div>
                                <div class="uoD _rg">{{info.invoice.banknumber}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">地址：</div>
                                <div class="uoD _rg">{{info.invoice.address}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">电话：</div>
                                <div class="uoD _rg">{{info.invoice.phone}}</div>
                            </div>
                        </div>
                        <!-- 付款信息 -->
                        <div v-if="info.is_pay==1">
                            <div class="hr mt15"></div>
                            <h1 class="order_tit mt15">付款信息</h1>
                            <div class="uoD_item">
                                <div class="uoD_lf">付款方式：</div>
                                <div class="uoD_rg">{{info.pay_from}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">付款时间：</div>
                                <div class="uoD_rg">{{info.payTime}}</div>
                            </div>
                            <div class="uoD_item">
                                <div class="uoD_lf">商品总额：</div>
                                <div class="uoD_rg">{{info.price_total}}</div>
                            </div>
                        </div>
                        <!-- 金额 -->
                        <div class="tr mt15">
                            商品总价：<span class="colorPrice us_price">{{info.price_total}}元</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    import {getOrderInfo,orderCancel,orderConfirm} from '../../../service/api';

    export default {
        name: 'order',
        data(){
            return{
                order_id:'',
                info:{
                    invoice:''
                }
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),
            waitPrice:function (){
                return this.info.price_total - this.info.is_pay
            }
        },
        methods:{
            async initData(){
                this.order_id = this.$route.query.order_id || '';
                if (this.userInfo && this.userInfo.user_id){
                    this.info = await getOrderInfo(this.order_id);
                    for(var i=0;i < this.info.goods.length; i++){
                        let price = this.info.goods[i].price *100;
                        let total= parseInt(this.info.goods[i].number)*parseInt(price)/100;
                        this.info.goods[i].itotal = total.toFixed(2);
                    }
                }
                console.log(this.info);

            },
            cancelOrder(order_id){
                console.log(order_id);
                this.$confirm('你确定要取消该订单吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderCancel(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });

            },
            toEvaluate(order_id){
                this.$router.push({path:"/userCenter/evaluate/evaluate",query:{order_id:order_id}});
            },
            payNow(orderId){
                this.$router.push({path:"/order/pay",query:{orderId:orderId}});
            },
            orderConfirm(order_id){
                console.log("order_id:"+this.order_id);
                this.$confirm('是否确认收货?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    let res = await orderConfirm(order_id);
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {
                });
            },
            goBack() {
                this.$router.go(-1);
            },

        },
        created(){

        },
        watch:{
            userInfo: function (value) {
                if (value && value.user_id && !this.list) {
                    this.initData();
                }
            }
        }

    }

</script>

<style scoped>
    /*订单详情*/
    .uc_od_head{background-color: #f5f5f5;line-height: 30px;padding:5px 15px;font-size: 13px;margin-top:15px;}
    .uod_btn{margin-right: 15px;font-size: 13px;height: 30px;line-height: 30px;}
    .stateText{font-size: 18px;font-weight: bold;margin-left:10px;}
    .ucod_pro{padding:10px 0;border-bottom:1px solid #eee;font-size:14px;}
    .od_img{width: 80px;height: 80px;}
    .od_img img{max-height: 80px;max-width: 80px;}
    .od_des{width:40%;}
    .od_num{width: 15%;}
    .od_uPrice{width:15%;}
    .od_price{width: 30%;}
    .od_info{padding-left: 15px;color:#999;font-size: 12px;line-height: 20px;}
    .od_info h2{color:#333;font-size: 14px;min-height: 40px;}

    .uoD_tit{line-height: 30px;margin-top: 15px;}
    .uoD_tit .defaultBtn{font-size: 13px;height: 30px;line-height: 30px;}
    .uoD_item{display: flex;flex-wrap: nowrap;}
    .uoD_lf{width:80px;font-size: 14px;}
    .uoD_rg{font-size: 14px;}

    .uoD_s_rg{background-color: #f5f5f5;height: 140px;overflow-y:auto;width: 450px;padding:15px;}
    .us_box{border-left: 1px solid #ddd;min-height: 140px;font-size: 12px;}
    .us_item{padding-left: 20px;position: relative;margin-bottom: 10px;line-height: 20px;}
    .us_spot{width:12px;height: 12px;position: absolute;left:-6px;top:6px;z-index: 100;background-color: #ccc;border-radius: 30px;}
    .us_item.active{color:#0e79cc;}
    .us_item.active .us_spot{background-color: #0e79cc;border:3px solid #8bbde3;left:-9px;}
    .us_price{font-size: 28px;font-weight: bold;}

    .order_tit{
        color:#333;
        padding-left: 8px;
        border-left: 5px solid #0bb8b2;
        line-height: 20px;
        margin:10px 0;
        font-size: 16px;
    }
</style>



